<template>
  <div id="home">
    <!--  -->
    <nav-bar class="home-nav">
      <div slot="left">
        <img class="home_left_img" src="~assets/img/home/sm.png" alt />
      </div>
      <div slot="center">
        <div class="home_search">
          <!--  -->
          <input type="text" class="home_search_in" placeholder="Vue NiuBi !!! DSW NiuBi !!!" />
          <!--  -->
          <img class="home_center_img" src="~assets/img/home/search.png" alt />
        </div>
      </div>
      <div slot="right" @click="dswClick">DSW</div>
    </nav-bar>
    <!--  -->
    <div class="dswNiubi" :class="{dswNiubiShow:isDsw}">
      <span> NiuBi !!!</span>
      <span> NiuBi !!!</span>
    </div>
    <!-- tab -->
    <tab-control
      :titles="titles"
      @tabClick="tabClick"
      ref="tabControl1"
      class="tab-control"
      v-show="isTabFixed"
    />
    <!-- better-scroll -->
    <scroll
      class="content"
      ref="scroll"
      :probe-type="3"
      :pull-up-load="true"
      @scroll="contentScroll"
      @pullingUp="loadMore"
    >
      <!--  -->
      <xinxin />
      <!--  -->
      <home-swiper :banners="banners" @swiperImageLoad.once="swiperImageLoad" />
      <!--  -->
      <recommend-view :recommends="recommends" />
      <!--  -->
      <feature-view />
      <!-- tab -->
      <tab-control :titles="titles" @tabClick="tabClick" ref="tabControl2" />
      <!--  -->
      <goods-list :goods="showGoods" />
    </scroll>
    <!--  -->
    <back-top @click.native="backClick" v-show="isShowBackTop" />
  </div>
</template>

<script>
import HomeSwiper from "./childComps/HomeSwiper";
import RecommendView from "./childComps/RecommendView";
import FeatureView from "./childComps/FeatureView";

import NavBar from "components/common/navbar/NavBar";
import TabControl from "components/content/tabControl/TabControl";
import GoodsList from "components/content/goods/GoodsList";
import Scroll from "components/common/scroll/Scroll";
import BackTop from "components/content/backTop/BackTop";
import xinxin from "components/content/xinxin/xinxin";

import { getHomeMultidata, getHomeGoods } from "network/home";
import { debounce } from "common/untils";

export default {
  name: "home",
  components: {
    HomeSwiper,
    RecommendView,
    FeatureView,
    NavBar,
    TabControl,
    GoodsList,
    Scroll,
    BackTop,
    xinxin
  },
  data() {
    return {
      banners: [],
      recommends: [],
      isDsw: false,
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] }
      },
      titles: ["", "", ""],
      // 
      titlesType: ["pop", "new", "sell"],
      // pop
      goodsType: "pop",
      // backTop
      isShowBackTop: false,
      // tabControld
      isTabFixed: false,
      // index
      tabIndex: 0,
      // tab
      tabScroll: [0, 0, 0],
      // 
      scrollTime: 500,
      // 
      saveY: 0
    };
  },
  computed: {
    showGoods() {
      return this.goods[this.goodsType].list;
    }
  },
  activated() {
    this.$refs.scroll.refresh();
    this.$refs.scroll.scrollTo(0, this.saveY, 0);
    // 
  },
  deactivated() {
    // 
    this.saveY = this.$refs.scroll.scroll.y;
  },
  // 
  created() {
    // 
    this.getHomeMultidata();

    // 
    this.getHomeGoods("pop");
    this.getHomeGoods("new");
    this.getHomeGoods("sell");
  },
  mounted() {
    // item
    const refresh = debounce(this.$refs.scroll.refresh, 200);
    this.$bus.$on("itemImageLoad", () => {
      refresh();
    });
  },
  methods: {
    /**
     * 
     */
    // tab
    tabClick(index) {
      // 
      this.goodsType = this.titlesType[index];
      // index
      this.tabIndex = index;

      // tabcurrentIndex
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;

      // 
      this.$refs.scroll.scrollTo(0, -this.tabScroll[index], this.scrollTime);

      //
    },
    // 
    backClick() {
      this.$refs.scroll.scrollTo(0, 0);
    },
    // 
    contentScroll(position) {
      // backTop
      this.isShowBackTop = -position.y > 1000;

      //tabControl
      this.isTabFixed = -position.y >= this.tabOffsetTop - 70;

      // tabIndex

      // tab
      if (this.isTabFixed) {
        this.tabScroll[this.tabIndex] = -position.y;
        // 0
        this.scrollTime = 0;
      } else {
        // tab
        for (let i = 0; i < this.tabScroll.length; i++) {
          this.tabScroll[i] = this.tabOffsetTop - 70;
          // 500
          this.scrollTime = 500;
        }
      }
    },
    // 
    loadMore() {
      this.getHomeGoods(this.goodsType);
    },
    // tabControloffsetTop
    swiperImageLoad() {
      // $el:
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop;
    },
    // DSW
    dswClick() {
      // 
      this.isDsw = true;
      // 3s
      setTimeout(() => {
        this.isDsw = false;
      }, 3000);
    },
    /**
     *
     */
    getHomeMultidata() {
      getHomeMultidata().then(res => {
        this.banners = res.data.banner.list;
        this.recommends = res.data.recommend.list;
      });
    },
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then(res => {
        this.goods[type].list.push(...res.data.list);
        this.goods[type].page += 1;

        // 
        this.$refs.scroll.finishPullUp();
      });
    }
  }
};
</script>

<style scoped>
#home {
  padding-top: 44px;
  height: 100vh;
}
.home-nav {
  position: fixed;
  background: var(--color-tint);
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
}
#zw {
  position: relative;
  width: 100%;
  height: 1000px;
  background: #eee;
}
.home_search {
  position: relative;
  top: 6px;
  width: 100%;
  height: 28px;
  border: none;
  border-radius: 14px;
  background: #fff;
}
.home_left_img {
  position: relative;
  width: 26px;
  top: 7px;
  line-height: 26px;
}
.home_search_in {
  position: relative;
  width: 90%;
  height: 26px;
  border: none;
  top: -10px;
  outline: none;
  font-size: 14px;
}
.home_center_img {
  position: absolute;
  width: 20px;
  top: 3px;
  right: 10px;
  background: #fff;
}
.content {
  position: absolute;
  overflow: hidden;
  top: -26px;
  bottom: 2px;
  left: 0;
  right: 0;
  background: #fff;
}
.tab-control {
  position: relative;
  z-index: 999;
  top: -1px;
}
.dswNiubi {
  position: absolute;
  width: 100%;
  height: 44px;
  background: black;
  z-index: 999;
  top: -44px;
  color: #b34ffd;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
  line-height: 44px;
  transition: 0.5s;
}
.dswNiubiShow {
  top: 0;
}
</style>